import React, { Suspense, lazy, useMemo } from "react";
import { useLocation, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

const HeroSection = lazy(() => import("./HeroSection"));
const ServiceList = lazy(() => import("./ServiceList"));
const ReviewsSection = lazy(() => import("./ReviewsSection"));
const FAQSection = lazy(() => import("./FAQSection"));
const Contact = lazy(() => import("./Contact"));
const MapSection = lazy(() => import("./MapSection"));
const BlogsAndForm = lazy(() => import("../blogs/Blogs"));

const HomePage = () => {
  const { t } = useTranslation(); // استخدام الترجمة
  const location = useLocation();

  // استخدام queryParams للحصول على الـ type من URL
  const queryParams = useMemo(
    () => new URLSearchParams(location.search),
    [location.search],
  );
  const typeFromParam = queryParams.get("type"); // الحصول على type من معلمات الـ URL

  // تحديد الـ type بين القيمتين (من الـ URL أو يدويًا)
  const type = typeFromParam || "project"; // إما من الـ URL أو يدويًا

  return (
    <div>
      <Suspense fallback={<div>Loading...</div>}>
        <HeroSection />
        <BlogsAndForm manualType={type} />
        <ServiceList limit={3} />
        <ReviewsSection limit={3} />
        <BlogsAndForm manualType="new" />
        <FAQSection />
        <Contact />
        <MapSection />
      </Suspense>

      {/* قسم "اعمل معنا" */}
      <div className="work-with-us-section">
        <h2>{t("home.workWithUsTitle")}</h2>
        <p>{t("home.workWithUsDescription")}</p>
        <Link to="/signup" className="btn btn-primary">
          {t("home.signUpNow")}
        </Link>
      </div>
    </div>
  );
};

export default HomePage;
